import React, { Component } from 'react';
import axios from 'axios';
import Preloader from './img/loader.gif';

class WikiQuery extends Component {
	constructor(props) {
		super(props);
		this.state = {
    	loading: true,
    	data: []
		}
	}


	componentDidMount() {
		this.getWikiData()
	}

	async getWikiData(){
		axios.get('https://query.wikidata.org/sparql?query=SELECT%20%3Fpersona%20%3FpersonaLabel%20WHERE%20%7B%0A%20%20%3Fpersona%20wdt%3AP106%20wd%3AQ1650915%3B%0A%20%20%20%20wdt%3AP21%20wd%3AQ6581072%3B%0A%20%20%20%20wdt%3AP27%20wd%3AQ298.%0A%20%20SERVICE%20wikibase%3Alabel%20%7B%20bd%3AserviceParam%20wikibase%3Alanguage%20%22%5BAUTO_%20LANGUAGE%5D%2Ces%22.%20%7D%0A%7D&format=json').then(response => {
      this.setState({
        loading: false,
        data: response.data.results.bindings
      })
    });
	}


	render(){
		var investigadores = this.state.data;
		var content = <div className="investigadores wiki">
					{investigadores.map( (person, index) => (
						<div key={index} className="investigador-container" >
							<div className="info">
								<h2>{ person.personaLabel.value }</h2>
								<a href={person.persona.value} target="_blank">Ver en WikiData</a>
							</div>
						</div>
					))}
				</div>

		return (
			<div className="wikiQuery">
				{this.state.loading ? <div className="preloader-container"><img src={Preloader} alt="loading" className="preloader" /></div> : content }
			</div>
		)
	}
}

export default WikiQuery;