import React, { Component } from 'react';

class InvestigadoresList extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	loading: true,
    	format: this.props.format,
      investigadoresJson: [],
      jsonData: {},
      investigadoresXml: [],
      view: "list",
      investigadorSelected: {}
    };
    this.selectInvestigador = this.selectInvestigador.bind(this);
  }

	componentDidMount() {
		//this.props.format === "json" ? this.getJson() : this.getXml();
		// this.getJson();
		// this.getXml()
	}

	selectInvestigador(e, index){
		this.props.selectInvestigador("", index);
	}


	render(){
		var investigadores = this.props.investigadores;
		return (
			<div className="investigadores-container">
				<div className="investigadores">
					{investigadores.map( (person, index) => (
						<div key={index} className="investigador-container" onClick={ (e) => this.selectInvestigador(e, index) }>
							<div className="foto">
								<img src={ person.foto } alt={ 'Imagen de ' + person.nombre + ' ' + person.apellidos } />
							</div>
							<div className="info">
								<h2>{ person.nombre } { person.apellidos }</h2>
								<p>{ person.campo }</p>
							</div>
						</div>
					))}
				</div>
			</div>
		)
	}
}

export default InvestigadoresList;