import React, { Component } from 'react';
import axios from 'axios';
import InvestigadoresList from './InvestigadoresList';
import Investigador from './Investigador';
import Preloader from './img/loader.gif';

class InvestigadorContent extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	loading: true,
    	format: "json",
      investigadoresJson: [],
      investigadoresXml: [],
      investigadores: [],
      view: "list",
      investigadorSelected: {}
    };
    this.changeView = this.changeView.bind(this);
    this.changeFormat = this.changeFormat.bind(this);
    this.getJson = this.getJson.bind(this);
    this.getXml = this.getXml.bind(this);
    this.parseXml = this.parseXml.bind(this);
    this.selectInvestigador = this.selectInvestigador.bind(this);
  }

	componentDidMount() {
		this.getJson();
		this.getXml();

	}

	changeFormat(e, newFormat){
		var newData = newFormat === "json" ? this.state.investigadoresJson : this.state.investigadoresXml;
    this.setState({ format : newFormat, view: "list", investigadores: newData })
  }

	changeView(newView){
    this.setState({view: newView})
  }

	async getJson(){
		axios.get('info/investigadores.json').then(response => {
      setTimeout(()=>{
	      this.setState({
	        loading: false,
	        investigadoresJson: response.data.investigadores,
	        investigadores:  response.data.investigadores
	      })
	    }, 1000)
    });
	}

	async getXml(){
		var myData;
	  const response = await axios.get('info/investigadores.xml');

	  var parseString = require('xml2js').parseString;
		parseString(response.data, {trim: true}, function (err, result) {
  		myData = result.investigadores.investigador;
		});

		setTimeout(() => {
	    this.setState({
	      loading: false,
	      investigadoresXml: myData
	    })
	  }, 1000)

	}

	parseXml(xml) {
		var parseString = require('xml2js').parseString;
		parseString(xml, {trim: true}, function (err, result) {
  		return result.investigadores.investigador;
		});
	}

	selectInvestigador(e, index) {
		var investigadores = this.props.format === "json" ? this.state.investigadoresJson : this.state.investigadoresXml;
		//console.log(index);
		this.setState({
			investigadorSelected: investigadores[index],
			view: "person"
		})
		this.changeView("person");
	}

	render(){
		//var investigadores = this.state.format === "json" ? this.state.investigadoresJson : this.state.investigadoresXml;
		var investigadores = this.state.investigadores;
		var investigadorSelected = this.state.investigadorSelected;
		var view = this.state.view === "list" ? <InvestigadoresList investigadores={investigadores} selectInvestigador={this.selectInvestigador} /> : 
			<Investigador person={investigadorSelected} changeView={this.changeView} format={this.state.format} />
					
		return (
			<main>
				<div className="format-btns">
					<button className={ this.state.format === "json" ? "btn selected" : "btn"} onClick={ (e) => this.changeFormat(e, "json") }>JSON</button>
					<button className={ this.state.format === "xml" ? "btn selected" : "btn"} onClick={ (e) => this.changeFormat(e, "xml") }>XML</button>
				</div>
				{this.state.loading ? <div className="preloader-container"><img src={Preloader} alt="loading" className="preloader" /></div> : view }
			</main>
		)
	}
}

export default InvestigadorContent;