import React, {Component} from 'react';

class Header extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	showMenu: false,
    	content: this.props.content
    };
    this.toggleMenu = this.toggleMenu.bind(this);
  }

  changeContent(e, newContent){
		this.props.changeContent(newContent);
  }

	toggleMenu() {
		// console.log('oldState: ' + this.state.showMenu);
		var newState = this.state.showMenu ? false : true;
		this.setState({showMenu: newState});
		//console.log('newState: ' + this.state.showMenu);
	}

	render() {
		var menuClass = this.state.showMenu ? "show" : "hidden";
		var nav = <nav>
	            <button id="toggleMenu" onClick={this.toggleMenu}>Menu</button>
	            <ul className={'menu ' + menuClass }>
	              <li class="selected">JSON</li>
	              <li>XML</li>
	            </ul>
	          </nav>

	  return (
	      <header>
	        <div className="header-container">
		        <div className="container">
		          <h1>Investigadores</h1>
		        </div>
	        </div>
	      	<nav>
	      		<div className="container nav-container">
	            <ul className={'menu ' + menuClass }>
	              <li className={ this.props.content === "local" ? "selected" : ""} onClick={ (e) => this.changeContent(e, "local")}>Investigadores JSON/XML</li>
	              <li className={ this.props.content === "wiki" ? "selected" : ""} onClick={ (e) => this.changeContent(e, "wiki")}>Wiki Query</li>
	              <li className={ this.props.content === "instance" ? "selected" : ""} onClick={ (e) => this.changeContent(e, "instance")}>Wikibase MTI</li>
	            </ul>
	            <a href="https://github.com/carlabalbontin/MTI-476-Sistemas-y-Aplicaciones-Web/tree/master/1-XML-y-JSON" target="_blank" rel="noopener noreferrer">Ver Docs</a>
	           </div>
          </nav>
	      </header>

	  );

	}
}

export default Header;