import React, {Component} from 'react';
// import logo from './logo.svg';
import './App.scss';
import Header from './Header.js';
import AppContent from './AppContent.js';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      format: "json",
      content: "local",
      view: "list"
    };
    this.changeContent = this.changeContent.bind(this);
  }

  changeContent(newContent){
    this.setState({ content : newContent})
  }
  // changeFormat(newFormat){
  //   this.setState({ format : newFormat, view: "list" })
  // }
  changeView(newView){
    this.setState({view: newView})
  }

  render(){
    return (
      <div className="App">
        <Header content={this.state.content} changeContent={e => this.changeContent(e)} />
        <AppContent content={this.state.content} format={this.state.format} view={this.state.view} changeView={e => this.changeView(e)} />
      </div>
    );

  }
}

export default App;
