import React, { Component } from 'react';
import axios from 'axios';
import Preloader from './img/loader.gif';

class WikibaseInstance extends Component {
	constructor(props) {
		super(props);
		this.state = {
    	loading: true,
    	data: []
		}
	}


	componentDidMount() {
		this.getData()
	}

	async getData(){
		axios.get('https://qs.getede.cl/proxy/wdqs/bigdata/namespace/wdq/sparql?query=SELECT%20%3FpersonaLabel%20%3FramoLabel%20%3Fnombre%20WHERE%20%7B%0A%20%20%3Fpersona%20wdt%3AP31%20wd%3AQ8%3B%0A%20%20%20%20wdt%3AP30%20%3Framo.%0A%20%20SERVICE%20wikibase%3Alabel%20%7B%20bd%3AserviceParam%20wikibase%3Alanguage%20%22es%22.%20%7D%0A%20%20OPTIONAL%20%7B%20%3Framo%20wdt%3AP2%20%3Fnombre.%20%7D%0A%7D&format=json').then(response => {
      this.setState({
        loading: false,
        data: response.data.results.bindings
      })
    });
	}


	render(){
		//var content = <h2>Wikibase</h2>;
		var investigadores = this.state.data;
		var content = <div className="investigadores wiki">
					{investigadores.map( (person, index) => (
						<div key={index} className="investigador-container" >
							<div className="info">
								<h2>{ person.personaLabel.value }</h2>
								<p>{ person.ramoLabel.value } - { person.nombre.value }</p>
							</div>
						</div>
					))}
				</div>
		return (
			<div className="wikiInstance">
				{this.state.loading ? <div className="preloader-container"><img src={Preloader} alt="loading" className="preloader" /></div> : content }
			</div>
		)
	}
}

export default WikibaseInstance;