import React, { Component } from 'react';
import InvestigadorContent from './InvestigadorContent';
import WikiQuery from './WikiQuery';
import WikibaseInstance from './WikibaseInstance';

class AppContent extends Component {
	constructor(props) {
		super(props);
	}


	render(){
		var content;
		if(this.props.content === "local"){
			content = <InvestigadorContent />
		} else if(this.props.content === "wiki") {
			content = <WikiQuery />
		} else {
			content = <WikibaseInstance />
		}
		return (
			<main className="appContent">
				{content}
			</main>
		)
	}
}

export default AppContent;